import React, { useState } from 'react';
import {useHistory, useLocation} from 'react-router-dom';
import { UpArrow } from '../SvgSprite/SvgSprite';
import { itemsCount } from '../../helpers/constants';
import cn from 'classnames';

let sortParams = [
  {title: 'Newest', value: 'age', typeParams: 'number'},
  {title: 'Price high to low', value:'high_price'},
  {title: 'Price low to high',  value:'low_price'}
]

export const SortBlock = () => {
  const location = useLocation();
  const searchParams = new URLSearchParams(location.search);
  const history = useHistory();
  const [activeParamsSelect, setActiveParamsSelect] = useState<boolean>(false);
  const [activeCountSelect, setActiveCountSelect] = useState<boolean>(false);

  const handleSortItemsCount = (e: React.ChangeEvent<HTMLSelectElement>) => {
    searchParams.set('perPage', e.target.value);
    history.push({
      search: searchParams.toString()
    });
  }

  const handleSortItemsCategory = (e: React.ChangeEvent<HTMLSelectElement>) => {
    searchParams.set('sortBy', e.target.value);
    history.push({
      search: searchParams.toString()
    });
  }

  if (!itemsCount.includes(Number(searchParams.get('perPage')))) {
    searchParams.set('perPage', String(8));
    searchParams.set('page', String(1));
    history.push({
      search: searchParams.toString(),
    });
  }

  if (!sortParams.map(item => item.value).includes(searchParams.get('sortBy') || '')) {
    searchParams.set('sortBy', 'age');
    searchParams.set('page', String(1));
    history.push({
      search: searchParams.toString(),
    });
  }

  return (
  <section className="catalog__sort">
    <form className="catalog__sort-form">
      <div className={cn('catalog__sort-by-params', {'active': activeParamsSelect})}>
        <label className="catalog__sort-title">
            Sort by
        </label>
        <UpArrow />
        <select
          onClick={() => setActiveParamsSelect(!activeParamsSelect)}
          className="catalog__sort-select"
          onChange={(e) => handleSortItemsCategory(e)}
          value={searchParams.get('sortBy') || ''}
          onBlur={() => setActiveParamsSelect(false)}
        >
          {sortParams.map(param => (
            <option
              className="catalog__sort-select-option"
              key={param.title}
              value={param.value}
            >
              {param.title}
            </option>
          ))}
        </select>
      </div>

      <div className={cn('catalog__sort-by-count', {'active': activeCountSelect})}>
        <label className="catalog__sort-title">
          Items on page
        </label>
        <UpArrow />
        <select
          onClick={() => setActiveCountSelect(!activeCountSelect)}
          className="catalog__sort-select"
          onChange={(e) => handleSortItemsCount(e)}
          value={searchParams.get('perPage') || ''}
          onBlur={() => setActiveCountSelect(false)}
        >
          {itemsCount.map(count => (
            <option
              key={count}
              className="catalog__sort-select-option"
              value={count}
            >
              {count}
            </option>
          ))}
        </select>
      </div>
    </form>
  </section>
  )
}
